// import  {useState} from 'react'
import reactDOM from 'react-dom'
import "./index.css"

var strokeWidth = 6 
var colorLine   = "white"

var itens = [2,2,2,2,2,2,2,2,2]
var possibilits = [
                    [0,1,2],
                    [3,4,5],
                    [6,7,8],

                    [0,3,6],
                    [1,4,7],
                    [2,5,8],

                    [0,4,8],
                    [2,4,6]
                  ]
var player = 1
function setPlayer(n){player = n}

let App = {       
  clicked(number){
    if (itens[number] !== 0 && itens[number] !== 1){
      itens[number] = 1
      setPlayer(0)
      App.reload()
    }
  }, 
  
  reload(){
    this.bot()
    reactDOM.render(<Main/>, document.getElementById('root'))
    this.whoWon()
  },

  bot(){
    function getRA(min, max) {
      return Math.random() * (max - min) + min;
    }
    if(player === 0){
      let list = []
      for (var i in itens){
        if (itens[i] !== 1 && itens[i] !== 0){
          list.push(i)
        } 
      }
      let g = getRA(0, list.length-1).toFixed()
      itens[list[g]] = 0  
      setPlayer(1)
  }
  },

  whoWon(){

    var answer = 0
    var p = 2
    for (var i of possibilits){
      if(itens[i[0]] === itens[i[1]] && itens[i[1]] === itens[i[2]]){
        if(itens[i[0]] !== 2){
          p = itens[i[0]]
          answer = true
        }
      }
    }
    
    if (p !== 2){
      reactDOM.render(<Result r={p} />, document.getElementById('root'))
    }
  },

}

function Result(props){
  if (props.r === 1){
    return(
      <div id="result">
        AEEEEEEEEEEEEEEEEEEEEEEEEE TU VENCEU MEU BOM!!!!!!
      </div>
    )
  }
  else{
    return(
      <div id="result">
        Tu perdeu pro pior bot da hist√≥ria cara kkkkkkkkkkkkkk
      </div>
    )
  }
}

function Option(props){
  if(props.ap === 0){
    return(
      <svg width="100" height="100">
        <circle cx="50%" cy="50%" r="30%" stroke={colorLine} strokeWidth={strokeWidth} fill="none"/>
      </svg>
    )
  }else{
    if (props.ap === 1){
      return(
        <svg width="100" height="100">
          <line x1="80" y1="20" x2="20" y2="80" stroke={colorLine} strokeWidth={strokeWidth}/>
          <line x1="20" y1="20" x2="80" y2="80" stroke={colorLine} strokeWidth={strokeWidth}/>
        </svg>
      )
    }else{
      return(
        <svg width="100" height="100">
          <circle cx="50%" cy="50%" r="30%" fill="transparent"/>
        </svg>
      )
    }
  }
}

function Body(props){
  return(
      <table>
        <tbody>
          <tr>
              <td onClick={() => {App.clicked(0)}}>
                  <Option ap={props.n0} />
              </td>
              <td onClick={() => {App.clicked(1)}}>
                  <Option ap={props.n1}/>
              </td>
              <td onClick={() => {App.clicked(2)}}>
                  <Option ap={props.n2}/>
              </td>
          </tr>
          <tr>
              <td onClick={() => {App.clicked(3)}}>
                  <Option ap={props.n3}/>
              </td>
              <td onClick={() => {App.clicked(4)}}>
                  <Option ap={props.n4}/>
              </td>
              <td onClick={() => {App.clicked(5)}}>
                  <Option ap={props.n5}/>
              </td>
          </tr>
          <tr>
              <td onClick={() => {App.clicked(6)}}>
                  <Option ap={props.n6}/>
              </td>
              <td onClick={() => {App.clicked(7)}}>
                  <Option ap={props.n7}/>
              </td>
              <td onClick={() => {App.clicked(8)}}>
                  <Option ap={props.n8}/>
              </td>
          </tr>
        </tbody>
      </table>
  )};

  function Main(){
    let [a,b,c,d,e,f,g,h,i] = itens
    return  (
              <div id="real">
                <div id="header">
                  Tic Tac Toe
                </div>
                  <Body n0={a} n1={b} n2={c}
                        n3={d} n4={e} n5={f}
                        n6={g} n7={h} n8={i}
                    />
                <div id="footer">
                  Gabriel Botelho Malenowitch
                </div>
              </div>
              )
  }

App.reload()

